import { Component } from '@angular/core';
import { THUMBNAV_BUTTONNAMES} from './ButtonNames';
import { EventCentralSvc } from '../../events/EventCentralSvc';
import { THUMBNAVHTML } from './ThumbNavHtml';

@Component({
    selector: 'thumbnav-cmp',
    template: THUMBNAVHTML

})
export class ThumbNavCmp {

    private _prevIcon: string = "prev-button"; //icon class for previous button
    private _nextIcon: string = "next-button"; //icon class for next button
    private _prevName: string = THUMBNAV_BUTTONNAMES.prevButton; //name for previous button
    private _nextName: string = THUMBNAV_BUTTONNAMES.nextButton; //name for next button
    private _prevToolTip: string = 'Previous Image(s)/Clip(s)';
    private _nextToolTip: string = 'Next Image(s)/Clip(s)';

    private _prevLabel: string = 'Prev';
    private _nextLabel: string = 'Next';

    private _enabledList: any = {}; //map of button names to enabled state (boolean)
    private _selectedList: any = {}; //map of button names to selected state (boolean)

    public constructor(private _eventCentralSvc: EventCentralSvc) {
        for (let button in THUMBNAV_BUTTONNAMES) {
            this._enabledList[button] = true;
            this._selectedList[button] = false;
        }
    }

    public ngOnInit(): void {

        //listen for enable button events
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PREVBUTTONENABLE, (payload) => {
            this.updateEnabled(THUMBNAV_BUTTONNAMES.prevButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.NEXTBUTTONENABLE, (payload) => {
            this.updateEnabled(THUMBNAV_BUTTONNAMES.nextButton, payload.newState);
        });


        //listen for select button events
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PREVBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(THUMBNAV_BUTTONNAMES.prevButton, true);
            }
            this.updateSelected(THUMBNAV_BUTTONNAMES.prevButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.NEXTBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(THUMBNAV_BUTTONNAMES.nextButton, true);
            }
            this.updateSelected(THUMBNAV_BUTTONNAMES.nextButton, payload.newState);
        });

        window.addEventListener("keydown", (ev: any) => {this.checkKeyPressed(ev); } , false);

    }


    /**
     * returns enabled state of a button.
     * @param string: name of the button.
     * @return boolean: true is button is enabled, else false
     */
    public isButtonEnabled(buttonName: string): boolean {
        return this._enabledList[buttonName];
    }

    /**
     * returns selected state of a button.
     * @param string: name of the button
     * @return boolean: true is button is selected, else false
     */
    public isButtonSelected(buttonName: string): boolean {
        return this._selectedList[buttonName];
    }

    /**
     * updates enabled state of a button.
     * @param string: name of the button to update
     * @param boolean: true to enable button, false to diasable
     */
    public updateEnabled(buttonName: string, isEnabled: boolean): void {
        this._enabledList[buttonName] = isEnabled;
    }

    /**
     * updates selected state of a button.
     * @param string: name of the button to update
     * @param boolean: true to select button, false to unselect
     */
    public updateSelected(buttonName: string, isEnabled: boolean): void {
        this._selectedList[buttonName] = isEnabled;
    }

    /**
     * method that runs on button click. Sets button to selected and dispatches event
     * @param {buttonName: string} object that holds name of the button that was clicked
     */
    public buttonClick(buttonName: string): void {
        switch (buttonName) {
            case THUMBNAV_BUTTONNAMES.prevButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.PREVBUTTONCLICK, this.isButtonSelected(THUMBNAV_BUTTONNAMES.prevButton));
                break;
            case THUMBNAV_BUTTONNAMES.nextButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.NEXTBUTTONCLICK, this.isButtonSelected(THUMBNAV_BUTTONNAMES.nextButton));
                break;
            default:
                console.log("invalid button name");
        }

    }

    private checkKeyPressed(e: any): void {
        if (e.keyCode === 37) {
            e.preventDefault();
            this.buttonClick(THUMBNAV_BUTTONNAMES.prevButton);
        }
        if (e.keyCode === 39) {
            e.preventDefault();
            this.buttonClick(THUMBNAV_BUTTONNAMES.nextButton);
        }

    }
}
