import { Component, Input } from "@angular/core";
import { SCENARIO_BUTTONNAMES} from './ButtonNames';
import { EventCentralSvc } from '../../events/EventCentralSvc';
import { SCENARIOCONTROLHTML } from './ScenarioControlHtml';

@Component({
    selector: 'scenariocontrol-cmp',
    template: SCENARIOCONTROLHTML
})

export class ScenarioControlCmp {

    private _singleIcon: string = "icon-single"; //icon class for single view
    private _thumbnailIcon: string = "icon-thumbnails"; //icon class for single view
    private _dualIcon: string = "icon-dual"; //icon class for dual view
    private _quadIcon: string = "icon-quad"; //icon class for quad view
    private _singleName: string = SCENARIO_BUTTONNAMES.singleButton;
    private _thumbnailName: string = SCENARIO_BUTTONNAMES.thumbnailButton;
    private _dualName: string = SCENARIO_BUTTONNAMES.dualButton;
    private _quadName: string = SCENARIO_BUTTONNAMES.quadButton;
    private _singleToolTip: string = "Single View";
    private _thumbnailToolTip: string = "Thumbnail View";
    private _dualToolTip: string = "Dual View";
    private _quadToolTip: string = "Quad View";

    @Input() private _defaultSelection: string = '';

    private _enabledList: any = {}; //map of button names to enabled state (boolean)
    private _selectedList: any = {}; //map of button names to selected state (boolean)

    public constructor(private _eventCentralSvc: EventCentralSvc) {

    }

    public ngOnInit(): void {

        for (let button in SCENARIO_BUTTONNAMES) {
            this._enabledList[button] = true;
            this._selectedList[button] = (button === this._defaultSelection);
        }

        this._eventCentralSvc.addEventListener(this._eventCentralSvc.SINGLEBUTTONENABLE, (payload) => {
            this.updateEnabled(SCENARIO_BUTTONNAMES.singleButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.THUMBNAILBUTTONENABLE, (payload) => {
            this.updateEnabled(SCENARIO_BUTTONNAMES.thumbnailButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.DUALBUTTONENABLE, (payload) => {
            this.updateEnabled(SCENARIO_BUTTONNAMES.dualButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.QUADBUTTONENABLE, (payload) => {
            this.updateEnabled(SCENARIO_BUTTONNAMES.quadButton, payload.newState);
        });

        //listen for select button events
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.SINGLEBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(SCENARIO_BUTTONNAMES.singleButton, true);
            }
            this.updateSelected(SCENARIO_BUTTONNAMES.singleButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.THUMBNAILBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(SCENARIO_BUTTONNAMES.thumbnailButton, true);
            }
            this.updateSelected(SCENARIO_BUTTONNAMES.thumbnailButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.DUALBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(SCENARIO_BUTTONNAMES.dualButton, true);
            }
            this.updateSelected(SCENARIO_BUTTONNAMES.dualButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.QUADBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(SCENARIO_BUTTONNAMES.quadButton, true);
            }
            this.updateSelected(SCENARIO_BUTTONNAMES.quadButton, payload.newState);
        });

    }

    /**
     * returns enabled state of a button.
     * @return boolean: true is button is enabled, else false
     */
    public isButtonEnabled(buttonName: string): boolean {
        return this._enabledList[buttonName];
    }

    /**
     * returns selected state of a button.
     * @return boolean: true is button is selected, else false
     */
    public isButtonSelected(buttonName: string): boolean {
        return this._selectedList[buttonName];
    }

    /**
     * updates enabled state of a button.
     * @param string: name of the button to update
     * @param boolean: true to enable button, false to diasable
     */
    public updateEnabled(buttonName: string, isEnabled: boolean): void {
        this._enabledList[buttonName] = isEnabled;
    }

    /**
     * updates selected state of a button.
     * @param string: name of the button to update
     * @param boolean: true to select button, false to unselect
     */
    public updateSelected(buttonName: string, isEnabled: boolean): void {
        this._selectedList[buttonName] = isEnabled;
    }

    /**
     * method that runs on button click. Sets button to selected and dispatches event
     * @param {buttonName: string} object that holds name of the button that was clicked
     */
    public buttonClick(buttonName: string): void {
        switch (buttonName) {
            case SCENARIO_BUTTONNAMES.singleButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.SINGLEBUTTONCLICK, this.isButtonSelected(SCENARIO_BUTTONNAMES.singleButton));
                break;
            case SCENARIO_BUTTONNAMES.thumbnailButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.THUMBNAILBUTTONCLICK, this.isButtonSelected(SCENARIO_BUTTONNAMES.thumbnailButton));
                break;
            case SCENARIO_BUTTONNAMES.dualButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.DUALBUTTONCLICK, this.isButtonSelected(SCENARIO_BUTTONNAMES.dualButton));
                break;
            case SCENARIO_BUTTONNAMES.quadButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.QUADBUTTONCLICK, this.isButtonSelected(SCENARIO_BUTTONNAMES.quadButton));
                break;
            default:
                console.log("invalid button name");
        }
    }

}
