/**
 * Created by brian Air on 12/8/16.
 * This singleton provides access to the instance of any singleton component
 * that is stored here. The constructor of a component that wants this service to hold onto them, Does:
 * constructor(private _cmpSingletonSvc: CmpSingletonSvc) {
 *     _cmpSingletonSvc.viewerMainCmp = this; }
 *
 * Then when you want to message the component from some other class:
 * constructor(private _cmpSingletonSvc: CmpSingletonSvc ) {
 *
 * public testIt(): void {
 *      //Get the handle to the component
 *      let viewerMainCmp: ViewerMainCmp = this._cmpSingletonSvc.viewerMainCmp;
 *      //Call some methods on the component
 *      let a: string = viewerMainCmp.test;
 *      viewerMainCmp.test = "on";
 *      }
 */
import { Injectable } from "@angular/core";
import { ViewerMainCmp } from "./ViewerMainCmp";
import { ThumbnailListCmp } from "./thumbnailList/ThumbnailListCmp";
import { FullSizeDisplayCmp } from "./fullSizeDisplay/FullSizeDisplayCmp";
import { ThumbTotalNumCmp } from "./interactiveComponents/ThumbTotalNumCmp";

@Injectable()
export class CmpSingletonSvc {

    private _viewerMainCmp: ViewerMainCmp;
    private _thumbnailListCmp: ThumbnailListCmp;
    //private _mainControlBarCmp: MainControlBarCmp;
    private _fullSizeDisplayCmp: FullSizeDisplayCmp;
    private _thumbTotalNumCmp: ThumbTotalNumCmp;

    public get viewerMainCmp(): ViewerMainCmp {
        return this._viewerMainCmp;
    }

    public set viewerMainCmp(value: ViewerMainCmp) {
        this._viewerMainCmp = value;
    }

    // public get mainControlBarCmp(): MainControlBarCmp {
    //     return this._mainControlBarCmp;
    // }
    //
    // public set mainControlBarCmp(value: MainControlBarCmp) {
    //     this._mainControlBarCmp = value;
    // }
    //
    public get fullSizeDisplayCmp(): FullSizeDisplayCmp {
        return this._fullSizeDisplayCmp;
    }

    public set fullSizeDisplayCmp(value: FullSizeDisplayCmp) {
        this._fullSizeDisplayCmp = value;
    }

    public get thumbnailListCmp(): ThumbnailListCmp {
        return this._thumbnailListCmp;
    }

    public set thumbnailListCmp(value: ThumbnailListCmp) {
        this._thumbnailListCmp = value;
    }

    public get thumbTotalNumCmp(): ThumbTotalNumCmp {
        return this._thumbTotalNumCmp;
    }

    public set thumbTotalNumCmp(value: ThumbTotalNumCmp) {
        this._thumbTotalNumCmp = value;
    }
}
