/**
 * Component for displaying a video, and including the following functionalities:
 * 1. button click to play/pause a video;
 * 2. button click to show the previous/next frame of a video;
 * 3. event emitter passes frameID to its parent component;
 */

import { Component, Input, ElementRef, ViewChild, Output, EventEmitter, AfterViewInit, OnDestroy } from "@angular/core";
import { Document } from "../studyloader/Document";
import { EventCentralSvc } from "../events/EventCentralSvc";
import { VIDEOHTML } from './VideoHtml';

@Component({
    selector: 'video-cmp',
    template: VIDEOHTML
})

export class VideoCmp implements AfterViewInit, OnDestroy {
    @Output() private _fireFrameID: EventEmitter<number> = new EventEmitter<number>(); // emits event when frame id is changed
    @Input('aDoc') private _aDoc: Document; // a document object
    @Input('_brightness') private _brightness: number; // brightness percentage
    @Input('_contrast') private _contrast: number; // contrast percentage
    @ViewChild('videoTag') private _videoEle: ElementRef; // video element reference
    private _secondPerFrame: number; // seconds for a video's every frame
    private _FrameID: number = 1; // this default value stands for the 1st frame (the start point of a video)

    constructor(private _eventCentralSvc: EventCentralSvc) {}

    public ngAfterViewInit(): void {
        // handle the scenario when either video duration or/and frames number is zero
        if (this._aDoc.duration === 0 || this._aDoc.frames === 0) {
            this._FrameID = undefined;
        } else {
            this._secondPerFrame = this._aDoc.duration / this._aDoc.frames;
        }
        // add play button click listener
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PLAYBUTTONCLICK, this._playBtnClick);
        // add previous frame button click listener
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PREVFRAMEBUTTONCLICK, this._preFrameBtnClick);
        // add next frame button click listener
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.NEXTFRAMEBUTTONCLICK, this._nextFrameBtnClick);
    }

    public ngOnDestroy(): void {
        // remove play button click listeners
        this._eventCentralSvc.removeEventListener(this._eventCentralSvc.PLAYBUTTONCLICK, this._playBtnClick);
        // remove previous frame button click listener
        this._eventCentralSvc.removeEventListener(this._eventCentralSvc.PREVFRAMEBUTTONCLICK, this._preFrameBtnClick);
        // remove next frame button click listener
        this._eventCentralSvc.removeEventListener(this._eventCentralSvc.NEXTFRAMEBUTTONCLICK, this._nextFrameBtnClick);
    }

    /**
     * Variable represents a function to control video play/pause.
     * If a video is currently play, then it should change to pause state after button click;
     * If a video is currently pause, then it should change to play state after button click;
     * @param isPlaying a boolean value represent the button state
     */
    private _playBtnClick: Function = (isPlaying) => {
        if (isPlaying) {
            this._videoEle.nativeElement.pause();
            if (this._FrameID !== undefined) {
                this._FrameID = Math.floor(this._videoEle.nativeElement.currentTime / this._secondPerFrame) + 1;
                this._fireFrameID.emit(this._FrameID);
            }
        } else {
            this._videoEle.nativeElement.play();
            if (this._FrameID !== undefined) {
                // to hide frame number while playing video
                this._FrameID = 0;
                this._fireFrameID.emit(this._FrameID);
            }
        }
    };

    /**
     * Variable represents a function to move a video to the previous frame.
     * @param preFrameBtnClick a boolean value represents previous frame button click
     */
    private _preFrameBtnClick: Function = (preFrameBtnClick) => {
        if (this._FrameID !== undefined) {
            // if it is already the first frame, then by clicking previous button,
            // it should go back to the last frame
            if (this._FrameID === 1) {
                this._FrameID = this._aDoc.frames;
            } else {
                this._FrameID--;
            }
            this._videoEle.nativeElement.currentTime = this._secondPerFrame * (this._FrameID - 1);
            this._fireFrameID.emit(this._FrameID);
        }
    };

    /**
     * Variable represents a function to move a video to the next frame.
     * @param nextFrameBtnClick a boolean value represents next frame button click
     */
    private _nextFrameBtnClick: Function = (nextFrameBtnClick) => {
        if (this._FrameID !== undefined) {
            // if it is already the last frame, then by clicking next button,
            // it should go back to the first frame
            if (this._FrameID === this._aDoc.frames) {
                this._FrameID = 1;
            } else {
                this._FrameID++;
            }
            this._videoEle.nativeElement.currentTime = this._secondPerFrame * (this._FrameID - 1);
            this._fireFrameID.emit(this._FrameID);
        }
    };
}
