/**
 * Component for displaying a video or an image (depends on media type)
 * for a single display/viewport.
 */

import { Component, Input } from "@angular/core";
import { Document } from "../studyloader/Document";
import { EventCentralSvc } from "../events/EventCentralSvc";
import { SINGLEDISPLAYHTML } from "./SingleDisplayHtml";

@Component({
    selector: 'single-display-cmp',
    template: SINGLEDISPLAYHTML
})

export class SingleDisplayCmp {
    @Input('aDoc') private _aDoc: Document; // a document object
    @Input() private _brightness: number; // brightness percentage
    @Input() private _contrast: number; // contrast percentage
    private _frameID: number = 0; // frame id number, its range is 1 ~ number of frames. Initial value 0 means frame id haven't set yet

    /**
     * Method for getting the frame id number of a video.
     * @param frameID video frame id number
     */
    private setFrameID(frameID: number): void {
        this._frameID = frameID;
    }
}
