export const THUMBNAVHTML: string = `
    <div class="button-group thumb-nav-control">
        <button-cmp
            [_iconClass]="_prevIcon"
            [_selectedClass]="_prevIcon"
            [_selected]="isButtonSelected(_prevName)"
            [_disabled]="!isButtonEnabled(_prevName)"
            [_name]="_prevName"
            [_label]="_prevLabel"
            [_toolTip]="_prevToolTip"
            (click)="buttonClick(_prevName)">
        </button-cmp>
        <button-cmp
            [_iconClass]="_nextIcon"
            [_selectedClass]="_nextIcon"
            [_selected]="isButtonSelected(_nextName)"
            [_disabled]="!isButtonEnabled(_nextName)"
            [_name]="_nextName"
            [_label]="_nextLabel"
            [_toolTip]="_nextToolTip"
            (click)="buttonClick(_nextName)">
        </button-cmp>
    </div>
`;
