import { Component } from '@angular/core';
import { PLAYER_BUTTONNAMES} from './ButtonNames';
import { EventCentralSvc } from '../../events/EventCentralSvc';
import { PLAYERCONTROLHTML } from './PlayerControlHtml';

@Component({
    selector: 'playercontrol-cmp',
    template: PLAYERCONTROLHTML
})

export class PlayerControlCmp {

    private _imgSettingsIcon: string = "icon-adjust"; //icon class for image settings button
    private _playIcon: string = "icon-play"; //icon class for play button
    private _prevIcon: string = "icon-toStart"; //icon class for previous button
    private _nextIcon: string = "icon-toEnd"; //icon class for next button
    private _pauseIcon: string = "icon-pause"; //icon class for pause button

    private _imgSettingsToolTip: string = "Brightness/Contrast";
    private _playToolTip: string = "Play/Pause"; //icon class for play button
    private _prevToolTip: string = "Previous Frame"; //icon class for previous button
    private _nextToolTip: string = "Next Frame"; //icon class for next button

    private _imgSettingsName: string = PLAYER_BUTTONNAMES.imgSettingsButton; //name for image settings button
    private _playName: string = PLAYER_BUTTONNAMES.playButton; //name for play button
    private _prevName: string = PLAYER_BUTTONNAMES.prevFrameButton; //name for previous button
    private _nextName: string = PLAYER_BUTTONNAMES.nextFrameButton; //name for next button

    private _enabledList: any = {}; //map of button names to enabled state (boolean)
    private _selectedList: any = {}; //map of button names to selected state (boolean)

    public constructor(private _eventCentralSvc: EventCentralSvc) {
        for (let button in PLAYER_BUTTONNAMES) {
            this._enabledList[button] = true;
            this._selectedList[button] = false;
        }
    }

    public ngOnInit(): void {

        //listen for enable button events
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.IMGSETTINGSBUTTONENABLE, (payload) => {
            this.updateEnabled(PLAYER_BUTTONNAMES.imgSettingsButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PLAYBUTTONENABLE, (payload) => {
            this.updateEnabled(PLAYER_BUTTONNAMES.playButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PREVFRAMEBUTTONENABLE, (payload) => {
            this.updateEnabled(PLAYER_BUTTONNAMES.prevFrameButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.NEXTFRAMEBUTTONENABLE, (payload) => {
            this.updateEnabled(PLAYER_BUTTONNAMES.nextFrameButton, payload.newState);
        });


        //listen for select button events
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.IMGSETTINGSBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(PLAYER_BUTTONNAMES.imgSettingsButton, true);
            }
            this.updateSelected(PLAYER_BUTTONNAMES.imgSettingsButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PLAYBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(PLAYER_BUTTONNAMES.playButton, true);
            }
            this.updateSelected(PLAYER_BUTTONNAMES.playButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.PREVFRAMEBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(PLAYER_BUTTONNAMES.prevFrameButton, true);
            }
            this.updateSelected(PLAYER_BUTTONNAMES.prevFrameButton, payload.newState);
        });
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.NEXTFRAMEBUTTONSELECT, (payload) => {
            if (payload.newState === true) {
                this.updateEnabled(PLAYER_BUTTONNAMES.nextFrameButton, true);
            }
            this.updateSelected(PLAYER_BUTTONNAMES.nextFrameButton, payload.newState);
        });

        window.addEventListener("keydown", (ev: any) => {this.checkKeyPressed(ev); } , false);
    }


    /**
     * returns enabled state of a button.
     * @return boolean: true is button is enabled, else false
     */
    public isButtonEnabled(buttonName: string): boolean {
        return this._enabledList[buttonName];
    }

    /**
     * returns selected state of a button.
     * @return boolean: true is button is selected, else false
     */
    public isButtonSelected(buttonName: string): boolean {
        return this._selectedList[buttonName];
    }

    /**
     * updates enabled state of a button.
     * @param string: name of the button to update
     * @param boolean: true to enable button, false to diasable
     */
    public updateEnabled(buttonName: string, isEnabled: boolean): void {
        this._enabledList[buttonName] = isEnabled;
    }

    /**
     * updates selected state of a button.
     * @param string: name of the button to update
     * @param boolean: true to select button, false to unselect
     */
    public updateSelected(buttonName: string, isEnabled: boolean): void {
        this._selectedList[buttonName] = isEnabled;
    }

    /**
     * method that runs on button click. Sets button to selected and dispatches event
     * @param {buttonName: string} object that holds name of the button that was clicked
     */
    public buttonClick(buttonName: string): void {
        switch (buttonName) {
            case PLAYER_BUTTONNAMES.imgSettingsButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.IMGSETTINGSBUTTONCLICK, this.isButtonSelected(PLAYER_BUTTONNAMES.imgSettingsButton));
                break;
            case PLAYER_BUTTONNAMES.playButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.PLAYBUTTONCLICK, this.isButtonSelected(PLAYER_BUTTONNAMES.playButton));
                break;
            case PLAYER_BUTTONNAMES.prevFrameButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.PREVFRAMEBUTTONCLICK, this.isButtonSelected(PLAYER_BUTTONNAMES.prevFrameButton));
                break;
            case PLAYER_BUTTONNAMES.nextFrameButton:
                this._eventCentralSvc.dispatchEvent(
                    this._eventCentralSvc.NEXTFRAMEBUTTONCLICK, this.isButtonSelected(PLAYER_BUTTONNAMES.nextFrameButton));
                break;
            default:
                console.log("invalid button name");
        }
        if (buttonName === PLAYER_BUTTONNAMES.playButton) {
            this.updateSelected(buttonName, !this.isButtonSelected(buttonName));
            if (this.isButtonSelected(buttonName)) {
                this.updateEnabled(PLAYER_BUTTONNAMES.prevFrameButton, false);
                this.updateEnabled(PLAYER_BUTTONNAMES.nextFrameButton, false);
            } else {
                this.updateEnabled(PLAYER_BUTTONNAMES.prevFrameButton, true);
                this.updateEnabled(PLAYER_BUTTONNAMES.nextFrameButton, true);
            }
        }
    }

    private checkKeyPressed(e: any): void {
        if (e.keyCode === 32) {
            e.preventDefault();
            this.buttonClick(PLAYER_BUTTONNAMES.playButton);
        }

    }

}
