import { Component, Input } from '@angular/core';
import { BUTTONHTML } from './ButtonHtml';

@Component({
    selector: 'button-cmp',
    template: BUTTONHTML

})
export class ButtonCmp {
    @Input() private _disabled: boolean = false; //true if button has been disabled
    @Input() private _selected: boolean = false; //true is button is selected
    @Input() private _iconClass: string; //normal css class for the button
    @Input() private _selectedClass: string = ''; //special css class for selected state
    @Input() private _name: string; //name of this button
    @Input() private _label: string = ''; //label for button
    @Input() private _toolTip: string = '';
}
