import { Injectable, EventEmitter } from '@angular/core';
import { ViewerURLLoaderSvc } from "./ViewerURLLoaderSvc";
import { Document } from "./Document";
import { DocumentMgrSvc } from "./DocumentMgrSvc";
import { Observable } from "rxjs";
declare var Study_Json_Data: any;


@Injectable()
export class StudyLoaderSvc  {
    
    private _requestEvent: EventEmitter<any> =  new EventEmitter<any>();
    constructor(private _viewerURLLoaderSvc: ViewerURLLoaderSvc, private _documentMgrSvc: DocumentMgrSvc ) {
    }



    /**
     * Loads all the XML data from the core and gets a JSON representation.
     * Then we parse the JSON data and build documents.
     * @param url The full url of the study to get the XML data
     * @param debugMode When set to true, uses the 'url' value set in this
     * method.
     * @emits an Observable that emits true on success.
     */
    public loadStudy(url: string, debugMode: boolean= false): Observable<any> {
       
        if (debugMode) {
            //Add your debug URL's here.  You can use any server. Don't delete other user's url's. Last one wins
            //  url = "http://brian.trunk.echomd.com/1/echo/flash/?_fn=UView&id=056ff52f-6e36-498e-9fb8-7dc60b022722";
            url = 'https://development.ulqdev.com/1/echo/flash/?_fn=UView&id=a03c14dc-2b65-418b-ab01-31106889d242';
            // url = 'https://development.ulqdev.com/1/echo/flash/?_fn=UView&id=914ec7b3-22f5-41b0-bbc2-5c9db9a46e5e';     //single img
        }

        // if (!url) {
        //     url = 'study.xml'; //TODO: Make dynamic!
        // }

        this._viewerURLLoaderSvc.loadJSON(url)
        .subscribe(
            data => {
                this.jsonLoadStudy(data.data.ul);
                this._requestEvent.emit(true);
            },
            err => {
                if (!err.loggedIn && err.error === undefined) {
                    console.log("Not logged in!!!");
                    this._requestEvent.error({"error": "Not logged in"});
                } else {
                    console.log("Network error, " + err.error);
                    this._requestEvent.error({"error": "Network Error, " + err.error});
                }
            }
        );
        return this._requestEvent;
    }

    public getJSON(): Observable<any> {
        this.jsonLoadStudy(Study_Json_Data);
        this._requestEvent.emit(true);
        return this._requestEvent;
    }

    /**
     * Takes the study JSON object and extracts the study JSON object and
     * and passes the image data (files) to be extracted into documents.
     * @param json A JSON object that contains all the data pass from the core
     */
    private jsonLoadStudy(json: any): void {
        if (location.protocol === 'file:') {                
            let studyJson: any = json.ul.study.files;
            this.jsonLoadFiles(studyJson.file);
        } else {
            let studyJson: any = json.study;
            this.jsonLoadFiles(studyJson.files);
        }
    }

    /**
     * Loops through all the different file sets and adds each set to it's own document
     * @param json A JSON object that contains all the data for the study
     */
    private jsonLoadFiles(jsonFiles: any): void { 
        let fileList: any;
                                      
        if (location.protocol === "file:") {
            fileList = jsonFiles;
        } else {
            fileList = jsonFiles.file;
        }

        if (!Array.isArray(fileList)) {
            fileList = [fileList];
        }
        var cntId: number = 1;

        for (let file of fileList) {
            let attributes: any = file['@attributes'];
            let document: Document = new Document();
            document.uuid = attributes.uuid;
            document.drawing = attributes.drawing === '1';
            document.thumbUrl = attributes.thumb_url;
            document.fullUrl = attributes.full_url;
            document.type = attributes.type;
            document.height = parseInt(attributes.height, 10) || 0;
            document.width = parseInt(attributes.width, 10) || 0;
            document.theight = parseInt(attributes.theight, 10) || 0;
            document.twidth = parseInt(attributes.twidth, 10) || 0;
            document.frames = parseInt(attributes.frames, 10) || 0;
            document.duration = parseFloat(attributes.duration) || 0.0;
            document.cntId = cntId;
        
            this._documentMgrSvc.addDocument(document);
            cntId++;
        }
    }
}

