/**
 * Component for displaying images/videos based on view states.
 * There are total three view states: single view, dual view, quad view.
 */

import { Component, ChangeDetectorRef, AfterViewInit, OnDestroy } from "@angular/core";
import { Document } from "../studyloader/Document";
import { CmpSingletonSvc } from "../CmpSingletonSvc";
import { FULLSIZEDISPLAYHTML } from './FullSizeDisplayHtml';
import { EventCentralSvc } from "../events/EventCentralSvc";


@Component({
    selector: 'full-size-display-cmp',
    template: FULLSIZEDISPLAYHTML
})

export class FullSizeDisplayCmp implements AfterViewInit, OnDestroy {

    private _docList: Document[] = []; // a list of documents
    private _className: string = 'single-view'; // css class name for applying on different view state
    private _brightness: number = 100; // brightness percentage
    private _contrast: number = 100; // contrast percentage

    constructor(private _cmpSingletonSvc: CmpSingletonSvc, private _eventCentralSvc: EventCentralSvc,
                private _changeDetectorRef: ChangeDetectorRef) {
        _cmpSingletonSvc.fullSizeDisplayCmp = this;
    }

    /**
     * Method for changing css style based on view state.
     * @param numberOfDisplays number of displays/viewports should be there.
     */
    public createDisplayCmp(numberOfDisplays: number): void {
        switch (numberOfDisplays) {
            case 1: this._className = 'single-view';
                break;
            case 2: this._className = 'dual-view';
                break;
            case 4: this._className = 'quad-view';
                break;
            default: this._className = 'single-view';
        }
    }

    /**
     * Method for loading images based on document list.
     * @param documents a list of documents
     */
    public loadImages(documents: Document[]): void {
        // force *ngFor to remove the DOMs it created, and recreate DOMs
        this._docList = [];
        this._changeDetectorRef.detectChanges();
        this._docList = documents;
        this._changeDetectorRef.detectChanges();
    }

    public ngAfterViewInit(): void {
        // add listeners for brightness and contrast events
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.BRIGHTNESSSLIDER, this._changeBrightness);
        this._eventCentralSvc.addEventListener(this._eventCentralSvc.CONTRASTSLIDER, this._changeContrast);
    }

    public ngOnDestroy(): void {
        // remove listener for brightness and contrast events
        this._eventCentralSvc.removeEventListener(this._eventCentralSvc.BRIGHTNESSSLIDER, this._changeBrightness);
        this._eventCentralSvc.removeEventListener(this._eventCentralSvc.CONTRASTSLIDER, this._changeContrast);
    }

    /**
     * Variable represents brightness change function.
     * @param brightness a number represent brightness
     */
    private _changeBrightness: Function = (brightness) => {
        this._brightness = brightness.sliderValue;
    };

    /**
     * Variable represents contrast change function.
     * @param contrast a number represent contrast
     */
    private _changeContrast: Function = (contrast) => {
        this._contrast = contrast.sliderValue;
    };
}
