/**
 * Created by brian Air on 12/5/16.
 * A Document contains all the data for a image instance
 */

export class Document {

    private _cntID: number;
    private _uuid: string;
    private _drawing: boolean;
    private _thumbUrl: string;
    private _fullUrl: string;
    private _type: string;
    private _height: number;
    private _width: number;
    private _theight: number;
    private _twidth: number;
    private _frames: number;
    private _duration: number;

    public get cntId(): number {
        return this._cntID;
    }

    public set cntId(pCntID: number) {
        this._cntID = pCntID;
    }

    public get uuid(): string {
        return this._uuid;
    }

    public set uuid(pUuid: string) {
        this._uuid = pUuid;
    }

    public get drawing(): boolean {
        return this._drawing;
    }

    public set drawing(pDrawing: boolean) {
        this._drawing = pDrawing;
    }

    public get thumbUrl(): string {
        return this._thumbUrl;
    }

    public set thumbUrl(pThumbUrl: string) {
        this._thumbUrl = pThumbUrl;
    }

    public get fullUrl(): string {
        return this._fullUrl;
    }

    public set fullUrl(pFullUrl: string) {
        this._fullUrl = pFullUrl;
    }

    public get type(): string {
        return this._type;
    }

    public set type(pType: string) {
        this._type = pType;
    }

    public get height(): number {
        return this._height;
    }

    public set height(pHeight: number) {
        this._height = pHeight;
    }

    public get width(): number {
        return this._width;
    }

    public set width(pWidth: number) {
        this._width = pWidth;
    }

    public get theight(): number {
        return this._theight;
    }

    public set theight(pTheight: number) {
        this._theight = pTheight;
    }

    public get twidth(): number {
        return this._twidth;
    }

    public set twidth(pTwidth: number) {
        this._twidth = pTwidth;
    }

    get frames(): number {
        return this._frames;
    }

    set frames(value: number) {
        this._frames = value;
    }

    get duration(): number {
        return this._duration;
    }

    set duration(value: number) {
        this._duration = value;
    }
}
