import { Injectable } from "@angular/core";
import { EventCentralSvc } from "../events/EventCentralSvc";
import { ViewerMainCmp } from "../ViewerMainCmp";
import { SingleViewDelegate } from "./SingleViewDelegate";
import { DualViewDelegate } from "./DualViewDelegate";
import { QuadViewDelegate } from "./QuadViewDelegate";
import { IView } from "./IView";
import { Document } from "../studyloader/Document";
import { DocumentMgrSvc } from "../studyloader/DocumentMgrSvc";
import { CmpSingletonSvc } from "../CmpSingletonSvc";
import { ThumbnailViewDelegate } from "./ThumbnailViewDelegate";

@Injectable()
export class ViewStateChangeDelegate {

    constructor(private _viewerMainCmp: ViewerMainCmp,
                private _documentMgrSvc: DocumentMgrSvc,
                private _cmpSingletonSvc: CmpSingletonSvc,
                private _eventCentralSvc: EventCentralSvc) {

    }

    /**
     * Takes the current scenario view state and creates the correct delegate.  Then it sets the scenario
     * button state to selected. It then asks the delegate to create displays and loadimages.
     * @param viewState State of the Scenario view
     * @param firstViewDocument a Document object
     * @return An instance that implements IView interface
     */
    public viewStateChanged(viewState: string, firstViewDocument: Document): IView {
        let delegate: IView;
        this.initialButtonStates();
        switch (viewState) {
            case this._viewerMainCmp.THUMBNAILVIEW:
                delegate = new ThumbnailViewDelegate(this._cmpSingletonSvc, this._documentMgrSvc,
                                                     this._eventCentralSvc, this._viewerMainCmp);
                this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.THUMBNAILBUTTONSELECT, {newState: true});
                this._viewerMainCmp.displayDialog = false;
                // make brightness/contrast button unselected
                this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.IMGSETTINGSBUTTONSELECT, {newState: false});
                break;
            case this._viewerMainCmp.SINGLEVIEW:
                delegate = new SingleViewDelegate(this._cmpSingletonSvc, this._documentMgrSvc, this._eventCentralSvc, this._viewerMainCmp);
                this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.SINGLEBUTTONSELECT, {newState: true});
                break;
            case this._viewerMainCmp.DUALVIEW:
                delegate = new DualViewDelegate(this._cmpSingletonSvc, this._documentMgrSvc, this._eventCentralSvc, this._viewerMainCmp);
                this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.DUALBUTTONSELECT, {newState: true});
                break;
            case this._viewerMainCmp.QUADVIEW:
                delegate = new QuadViewDelegate(this._cmpSingletonSvc, this._documentMgrSvc, this._eventCentralSvc, this._viewerMainCmp);
                this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.QUADBUTTONSELECT, {newState: true});
                break;
            default:
        }

        this._viewerMainCmp.viewState = viewState;
        delegate.createDisplays();
        delegate.loadImages(firstViewDocument, false); // second parameter is set to false, to enable
        // scrolling and repositioning of selected thumbnail, at top of list of visible thumbnails.

        return delegate;
    }

    /**
     * Sets the initial state of buttons when scenario view changes;
     */
    private initialButtonStates(): void {
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.THUMBNAILBUTTONSELECT, {newState: false});
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.SINGLEBUTTONSELECT, {newState: false});
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.DUALBUTTONSELECT, {newState: false});
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.QUADBUTTONSELECT, {newState: false});
        //this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.PLAYBUTTONENABLE, {newState: true});
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.NEXTBUTTONENABLE, {newState: true});
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.PREVBUTTONENABLE, {newState: true});
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.IMGSETTINGSBUTTONENABLE, {newState: true});
        //this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.NEXTFRAMEBUTTONENABLE, {newState: true});
        //this._eventCentralSvc.dispatchEvent(this._eventCentralSvc.PREVFRAMEBUTTONENABLE, {newState: true});


    }
}
