export const THUMBTOTALNUMHTML: string = `
<!--Display in Thumbnail view-->
<span *ngIf="_startOfSelectedThumbID===0 && _endOfSelectedThumbID===0">
    {{_totalThumbNum}}
</span>
<!--Display in Single view-->
<span *ngIf="_startOfSelectedThumbID!==0 && _endOfSelectedThumbID===0">
    {{_startOfSelectedThumbID}} of {{_totalThumbNum}}
</span>
<!--Display in Dual/Quad view-->
<span *ngIf="_startOfSelectedThumbID!==0 && _endOfSelectedThumbID!==0">
    {{_startOfSelectedThumbID}} - {{_endOfSelectedThumbID}} of {{_totalThumbNum}}
</span>
`;
