/**
 * Component for displaying an image.
 */

import { Component, Input } from "@angular/core";
import { Document } from "../studyloader/Document";
import { STILLHTML } from './StillHtml';

@Component({
    selector: 'still-cmp',
    template: STILLHTML
})

export class StillCmp {
    @Input('aDoc') private _aDoc: Document; // a document object
    @Input('_brightness') private _brightness: number; // brightness percentage
    @Input('_contrast') private _contrast: number; // contrast percentage
}
