/**
 * Component used for displaying thumbnail images inside thumbnail list.
 */

import { Component, Input } from '@angular/core';
import { THUMBNAILHTML } from './ThumbnailHtml';
declare var viewerCdn: any;

@Component({
    selector: 'thumbnail',
    template: THUMBNAILHTML
})

export class ThumbnailCmp {
    @Input('thumbnailImg') private thumbnailImg: Object; // thumbnail image object that contains single thumbnail info
    private _stillToolTip: string = 'Image'; // tooltip for image icon
    private _clipToolTip: string = 'Clip'; // tooltip for video icon
    private _stillPath: string;
    private _clipPath: string;

    constructor() {
        let newCdnPath: string = 'app';
        if (typeof viewerCdn !== 'undefined') {
            newCdnPath = viewerCdn;
        }
        this._stillPath = newCdnPath + "/icon/still.png";
        this._clipPath = newCdnPath + "/icon/clip.png";
    }
}

