/* tslint:disable */
import { Injectable } from '@angular/core';
import { IEventMessage } from "./IEventMessage";
import { EventMessage } from "./EventMessage";


@Injectable()
export class EventCentralSvc {

    private _messages: any;

    //Add global event here
    public readonly BRIGHTNESSSLIDER: string = "brightnessSlider";
    public readonly CONTRASTSLIDER: string = "contrastSlider";
    public readonly PLAYBUTTONCLICK: string = "playButtonClick";
    public readonly NEXTBUTTONCLICK: string = "nextButtonClick";
    public readonly PREVBUTTONCLICK: string = "prevButtonClick";
    public readonly NEXTFRAMEBUTTONCLICK: string = "nextFrameButtonClick";
    public readonly PREVFRAMEBUTTONCLICK: string = "prevFrameButtonClick";
    public readonly IMGSETTINGSBUTTONCLICK: string = "imgSettingsButtonClick";
    public readonly SINGLEBUTTONCLICK: string = "singleButtonClick";
    public readonly THUMBNAILBUTTONCLICK: string = "thumbnailButtonClick";
    public readonly DUALBUTTONCLICK: string = "dualButtonClick";
    public readonly QUADBUTTONCLICK: string = "quadButtonClick";
    public readonly PLAYBUTTONENABLE: string = "playButtonEnable";
    public readonly NEXTBUTTONENABLE: string = "nextButtonEnable";
    public readonly PREVBUTTONENABLE: string = "prevButtonEnable";
    public readonly NEXTFRAMEBUTTONENABLE: string = "nextFrameButtonEnable";
    public readonly PREVFRAMEBUTTONENABLE: string = "prevFrameButtonEnable";
    public readonly IMGSETTINGSBUTTONENABLE: string = "imgSettingsButtonEnable";
    public readonly SINGLEBUTTONENABLE: string = "singleButtonEnable";
    public readonly THUMBNAILBUTTONENABLE: string = "thumbnailButtonEnable";
    public readonly DUALBUTTONENABLE: string = "dualButtonEnable";
    public readonly QUADBUTTONENABLE: string = "quadButtonEnable";
    public readonly PLAYBUTTONSELECT: string = "playButtonSelect";
    public readonly NEXTBUTTONSELECT: string = "nextButtonSelect";
    public readonly PREVBUTTONSELECT: string = "prevButtonSelect";
    public readonly NEXTFRAMEBUTTONSELECT: string = "nextFrameButtonSelect";
    public readonly PREVFRAMEBUTTONSELECT: string = "prevFrameButtonSelect";
    public readonly IMGSETTINGSBUTTONSELECT: string = "imgSettingsButtonSelect";
    public readonly SINGLEBUTTONSELECT: string = "singleButtonSelect";
    public readonly THUMBNAILBUTTONSELECT: string = "thumbnailButtonSelect";
    public readonly DUALBUTTONSELECT: string = "dualButtonSelect";
    public readonly QUADBUTTONSELECT: string = "quadButtonSelect";

    constructor() {
        this._messages = {};
    }

    /**
     * Adds an anonymous function to the list of callback functions for the message string event.  Duplicate addListeners on the
     * same message name and callback function will be ignored.
     * @param message: string The event message name as defined in the Public readonly of this class
     * @param callback: any The anonymous function that is associated with the message
     * @param priority: number Optional number that defines the priority of this callback event in relation to others
     *                          that are also on this message name.  A higher number means it will be sent first.
     **/
    addEventListener(message: string, callback: any, priority: number = 0): void {
        var msg: IEventMessage;
        msg = this._messages[message] || <IEventMessage>(this._messages[message] = new EventMessage());
        msg.addEventListener(callback, priority);
    }

    /**
     * Removes an anonymous function from the list of callback functions for the message string event.  The callback anonymous function
     * must be the same object for it to be removed from the event list.
     * @param message: string The event message name as defined in the Public readonly of this class
     * @param callback: any The anonymous function that is associated with the message}
     **/
    removeEventListener(message: string, callback: any): void {
        if (this._messages[message]) {
            (<IEventMessage>(this._messages[message])).removeEventListener(callback);
        }
    }

    /**
     * Sends the payload to all listeners of this message name event.
     * @param message: string The event message name as defined in the Public readonly of this class
     * @param payload: any Any object or data
     **/
    dispatchEvent(message: string, payload: any): void {
        if (this._messages[message]) {
            (<IEventMessage>(this._messages[message])).dispatchEvent(payload);
        }
    }

    /**
     * This is used if you have multiple listeners to a message event and a listener wants to prevent other
     * listeners from receiving the event.
     * @param message: string The event message name as defined in the Public readonly of this class
     **/
    stopProbagation(message: string): void {
        if (this._messages[message]) {
            (<IEventMessage>(this._messages[message])).stopProbagation();
        }
    }
}
/* tslint:enable */

