export const SINGLEDISPLAYHTML: string = `
 <div class="display-info-bar">
    <p class="display-order">
        <span>{{_aDoc.cntId}}</span>
        <span *ngIf="_aDoc.type==='clip' && _frameID!==0">- {{_frameID}}</span>
    </p>
</div>
<div [ngSwitch]="_aDoc.type" class="display-content">
    <still-cmp *ngSwitchCase="'still'"
               [aDoc]="_aDoc"
               [_brightness]="_brightness"
               [_contrast]="_contrast">
    </still-cmp>
    <video-cmp *ngSwitchCase="'clip'"
               (_fireFrameID)="setFrameID($event)"
               [aDoc]="_aDoc"
               [_brightness]="_brightness"
               [_contrast]="_contrast">
    </video-cmp>
    <p *ngSwitchDefault>neither image nor video</p>
</div>
`;
