/**
 * Component for displaying thumbnail images' total number,
 * the minimum and maximum id of thumbnails who displayed
 * as full size image/video on the viewport.
 */

import { Component } from "@angular/core";
import { CmpSingletonSvc } from "../CmpSingletonSvc";
import { THUMBTOTALNUMHTML } from "./ThumbTotalNumHtml";

@Component({
    selector: 'thumb-total-num-cmp',
    template: THUMBTOTALNUMHTML
})

export class ThumbTotalNumCmp {
    private _startOfSelectedThumbID: number = 0; // the minimum thumbnail id that current displayed on the full size viewport
    private _endOfSelectedThumbID: number = 0; // the maximum thumbnail id that current displayed on the full size viewport
    private _totalThumbNum: number = 0; // total number of thumbnails in a document list

    constructor(private _cmpSingletonSvc: CmpSingletonSvc) {
        _cmpSingletonSvc.thumbTotalNumCmp = this;
    }

    public get startOfSelectedThumbID(): number {
        return this._startOfSelectedThumbID;
    }

    public set startOfSelectedThumbID(value: number) {
        this._startOfSelectedThumbID = value;
    }

    public get endOfSelectedThumbID(): number {
        return this._endOfSelectedThumbID;
    }

    public set endOfSelectedThumbID(value: number) {
        this._endOfSelectedThumbID = value;
    }

    public get totalThumbNum(): number {
        return this._totalThumbNum;
    }

    public set totalThumbNum(value: number) {
        this._totalThumbNum = value;
    }
}
