/*
 * Component that represents the pop up menu for the brightness and contrast sliders.
 */



import { Component, Input } from '@angular/core';
import { EventCentralSvc } from "../events/EventCentralSvc";
import { BRIGHTNESSCONTRASTPOPUPHTML } from './BrightnessContrastPopupHtml';


@Component({
    selector: 'brightness-contrast-options-cmp',
    template: BRIGHTNESSCONTRASTPOPUPHTML
})

export class BrightnessContrastPopupCmp {


    /* Whether the sliders will be visible or not */
    @Input() private _displayDialog: boolean = false;


    /* The names of the sliders. Important to dispatch events via EventCentralSvc,
     * when their values change. */
    private BRIGHTNESS_SLIDER_NAME: string = "BRIGHTNESSSLIDER";
    private CONTRAST_SLIDER_NAME: string = "CONTRASTSLIDER";


    /* CSS classes for the brightness slider and the contrast slider, respectively */
    private _brightnessCSSClass: string = "brightnessSlider";
    private _contrastCSSClass: string = "contrastSlider";


    /* numerical values for the initialization of the brightness and contrast sliders, including
     * the default values on their scales and the maximum and minimum values on their scales */
    private _minValueBrightness: number = 0;
    private _maxValueBrightness: number = 200;
    private _currentValueBrightness: number = 100;

    private _minValueContrast: number = 0;
    private _maxValueContrast: number = 200;
    private _currentValueContrast: number = 100;


    constructor(private _eventCentralSvc: EventCentralSvc) {
    }


    /**
     * Updates the value for contrast and emits the latest value to all listeners.
     * @param newValueFromContrastSlider  _currentContrast will be set to this value
     */
    private updateContrastValue(newValueFromContrastSlider: number): void {
        this._currentValueContrast = newValueFromContrastSlider;
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc[this.CONTRAST_SLIDER_NAME],
            {'sliderValue': this._currentValueContrast});
    }

    /**
     * Updates the value for brightness and emits the latest value to all listeners.
     * @param newValueFromBrightnessSlider _currentBrightness will be set to this value
     */
    private updateBrightnessValue(newValueFromBrightnessSlider: number): void {
        this._currentValueBrightness = newValueFromBrightnessSlider;
        this._eventCentralSvc.dispatchEvent(this._eventCentralSvc[this.BRIGHTNESS_SLIDER_NAME],
            {'sliderValue': this._currentValueBrightness});
    }

    /**
     * Resets the brightness and contrast sliders to their middle values (100)
     */
    private resetSliders(): void {

        this.updateBrightnessValue(100);
        this.updateContrastValue(100);
    }

}
