/**
 * Component used for loading thumbnail images, and displaying them in a thumbnail list.
 * When click a thumbnail image, auto scroll to that thumbnail element.
 */

import {
    Component, ElementRef, ViewChild, ViewChildren, QueryList, AfterViewInit
} from '@angular/core';
import { DocumentMgrSvc } from "../studyloader/DocumentMgrSvc";
import { CmpSingletonSvc } from "../CmpSingletonSvc";
import { Document } from "../studyloader/Document";
import { ViewerMainCmp } from "../ViewerMainCmp";
import { THUMBNAILLISTHTML } from './ThumbnailListHtml';

@Component({
    selector: 'thumbnail-list-cmp',
    template: THUMBNAILLISTHTML
})

export class ThumbnailListCmp implements AfterViewInit {
    
    @ViewChild('thumbnail') private _thumbnailEle: ElementRef;
    @ViewChildren('thumbnail') private _thumbnails: QueryList<ElementRef>;

    private _documents: Document[]; // documents contains thumbnails info
    private _activeThumbnailID: number = 1; // by default, the first thumbnail image is active

    constructor(private _cmpSingletonSvc: CmpSingletonSvc,
                private _documentMgrSvc: DocumentMgrSvc,
                private _viewerMainCmp: ViewerMainCmp) {
        _cmpSingletonSvc.thumbnailListCmp = this;
    }

    /**
     * Method for listening to scroll event on thumbnail list, load images on the fly.
     */
    public ngAfterViewInit(): void {
        var myListener: any = this._thumbnails.changes
            .subscribe(
                newElement => {
                    this.scrollToThumbnail(this._activeThumbnailID);
                    myListener.unsubscribe();
                }

            );
     }

    /**
     * Method for setting documents data;
     */
    public documentLoaded(): void {
        this._documents = this._documentMgrSvc.documents;
    }

    /**
     * Method for positioning a thumbnail image.
     * @param document Document object contains thumbnail info
     * @param simpleThumbnailClick with value as false, scrolling and
     * repositioning of selected thumbnail, as the first visible thumbnail, occurs
     */
    public positionThumbnail(document: Document, simpleThumbnailClick: boolean): void {
        this._activeThumbnailID = document.cntId;
        if ((!simpleThumbnailClick) && (this._thumbnailEle)) {
            this.scrollToThumbnail(this._activeThumbnailID);
        }
    }

    /**
     * Method for scrolling a thumbnail element to the top of the viewport.
     * @param position number to indicate which thumbnail position to scroll to.
     */
    private scrollToThumbnail(position: number): void {
        this._thumbnails.toArray()[position - 1].nativeElement.scrollIntoView(true);

    }
    

    /**
     * Method for getting clicked document object and
     * change clicked thumbnail image style to active
     * when a click event happened.
     * @param aDocument thumbnail document object
     */
    private onClick(aDocument: Document): void {
        this._activeThumbnailID = aDocument.cntId;
        this._viewerMainCmp.thumbnailClicked(aDocument.cntId);
    }
}

