/**
 * Created by Brian Prince on 12/5/16.
 * Provides general purpose methods to put and get documents.
 * * Keeps an ordered list of documents as well as a hashed list.
 */
import { Injectable } from '@angular/core';
import { Document } from "./Document";

@Injectable()
export class DocumentMgrSvc {

    private _documents: Array<Document> = [];
    private _fileCntUuidMap: Array<any> = [];

    /**
     * Add a document to the lists
     * @param pDocument the document to add to the list
     */
    public addDocument(pDocument: Document): void {
        this._documents.push(pDocument);
        this._fileCntUuidMap[pDocument.cntId] = pDocument;
    }

    /**
     * returns an array of documents that is sorted by cntId
     * @returns An array of documents that is sorted by cntId
     */
    public get documents(): Array<Document> {
        return this._documents;
    }

    /**
     * gets a document for the supplied cntId
     * @returns a document
     */
    public getDocumentByCntId(cntId: number, diagram: boolean= false): Document {
        return this._fileCntUuidMap[cntId];
    }

    /**
     * Uses the cntId as the index and gets the document that is one less than
     * the cntId in the provided document. If the begining of the list is hit, a
     * undefined will be returned.
     * @param pDocument the document that contains the current cntId
     * @returns a document
     */
    public getPrevDocument(pDocument: Document): Document {
        let cntId: number = pDocument.cntId;
        cntId--;
        return this.getDocumentByCntId(cntId);
    }

    /**
     * Uses the cntId as the index and gets the document that is one more than
     * the cntId in the provided document. If the end of the list is hit, a
     * undefined will be returned.
     * @param pDocument the document that contains the current cntId
     * @returns a document
     */
    public getNextDocument(pDocument: Document): Document {
        let cntId: number = pDocument.cntId;
        cntId++;
        return this.getDocumentByCntId(cntId);
    }

    /**
     * Uses the cntId as the index and gets the number of specified documents in the list.
     * @param pDocument the document that contains the current cntId
     * @param count the number of documents to get
     * @returns an array of documents
     */
    public getNextDocuments(pDcoument: Document, count: number): Array<Document> {
        let documents: Array<Document> = [];
        let currentDocument: Document = pDcoument;
        for (let i: number = 1; i <= count; i++) {
            let aDocument: Document = this.getNextDocument(currentDocument);
            if (aDocument) {
                documents.push(aDocument);
                currentDocument = aDocument;
            } else {
                break;
            }
        }
        return documents;
    }

    /**
     * Returns the number of documents
     * @returns the number of documents
     */
    public numberOfDocuments(): number {
        let length: number = this._fileCntUuidMap.length - 1;
        if (length < 0) {
            length = 0;
        }
        return length;
    }
}
