import { Http, Response } from '@angular/http';
import { Injectable, EventEmitter } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

declare var XMLtoJSON: any;

@Injectable()
export class ViewerURLLoaderSvc {

    private _requestEvent: EventEmitter<any> =  new EventEmitter<any>();

    constructor(private _http: Http) {
        // TODO: CORS workaround (https://github.com/angular/angular/issues/4231).
        // tslint:disable
        let _build = (<any> _http)._backend._browserXHR.build;
        (<any> _http)._backend._browserXHR.build = () => {
            let _xhr = _build();
            _xhr.withCredentials = true;
            return _xhr;
        };
        // tslint:enable
    }

    /**
     * Does a HTTP request to the core and returns XML data for the study.
     * Then converts the XML data to JSON data.
     * @param url The full path to get the study's data
     * @emits an Observable that contains JSON representation of the XML data
     */
    public loadJSON(url: string): Observable<any> {
         this._http.get(url)
            .map((res: Response) => {
                let text: string = res.text();
                let leadingText: string = text.substring(0, 5);
                if (leadingText === "<?xml") {
                    //convert to json
                    let xml2json: any = new XMLtoJSON();
                    return xml2json.fromStr(text);
                } else {
                    console.log("Not logged in");
                    return undefined;
                }
            })
            .subscribe(
                data => {
                    if (data) {
                        this._requestEvent.emit({data});
                    } else {
                        this._requestEvent.error({"loggedIn": false, "data": undefined, "error": undefined}); // emit("true");
                    }
                },
                (err: string) => this._requestEvent.error({"loggedIn": false, "data": undefined, "error": "http failure"}) // emit("true");
            );
         return this._requestEvent;
    }
}
