export const THUMBNAILLISTHTML: string = `
    <ul class="thumbnailList-div">
        <li #thumbnail class="thumbnail-div"
            *ngFor="let thumbnailImg of _documents"
            [class.thumbnail-click]="thumbnailImg.cntId===_activeThumbnailID">
            <thumbnail [thumbnailImg]="thumbnailImg"
                    (click)="onClick(thumbnailImg)">
            </thumbnail>
        </li>
    </ul>
`;

