/**
 * Created by Brian Prince on 10/18/16.
 * Class that handles global event management on a event name basis.
 */
/* tslint:disable */
import { IEventMessage } from "./IEventMessage";

export class EventMessage implements IEventMessage {

    private _subscriptions: any[];
    private _stopProbagation: boolean = false;

    constructor() {
        this._subscriptions = [];
    }

    /**
     * Adds an anonymous function to the list of callback functions.  Duplicate addListeners on the
     * same message name and callback function will be ignored.
     * @param callback: any The anonymous function that is to be added to the list
     * @param priority: number Number that defines the priority of this callback event in relation to others.
     *                         A higher number means it will be sent first.
     **/
    public addEventListener(callback: any, priority: number): void {
        for (var index = 0; index < this._subscriptions.length; index++) {
            if (this._subscriptions[index].callback) {
                if (this._subscriptions[index].callback === callback) {
                    //event already exists, don't add it again
                    return;
                }
            }
        }

        this._subscriptions.push({callback: callback, priority: priority});

        // Re-sort subscribers so highest priority is first
        this._subscriptions = this._subscriptions.sort(function (a, b) {
            return b.priority - a.priority
        });
    }

    /**
     * Removes an anonymous function from the list of callback functions.  The callback anonymous function
     * must be the same object for it to be removed from the event list.
     * @param callback: any The anonymous function that is to be removed}
     **/
    public removeEventListener(callback: any): void {
        for (var index = 0; index < this._subscriptions.length; index++) {
            if (this._subscriptions[index].callback) {
                if (this._subscriptions[index].callback === callback) {
                    this._subscriptions.splice(index, 1);
                    break;
                }
            }
        }
    }

    /**
     * Sends the payload to all listeners of this message name event.
     * @param payload: any Any object or data
     **/
    public dispatchEvent(payload: any): void {
        this._stopProbagation = false;
        for (var index = 0; index < this._subscriptions.length; index++) {
            if (this._subscriptions[index].callback && !this._stopProbagation) {
                this._subscriptions[index].callback(payload);
            }
        }
    }

    /**
     * This is used if you have multiple listeners to a message event and a listener wants to prevent other
     * listeners from receiving the event.
     **/
    public stopProbagation(): void {
        this._stopProbagation = true;
    }
}
/* tslint:enable */

